export default {
    name: 'billCom',
    data () {
        return {
            downloadUrl: window.site + `misc/download_file/`,
            service: '',
            ticketTypeList4: ['RICHTEXT', 'TABLE', 'FILE', 'CUSTOMTABLE'],
            // ,
            OPERATE_TYPE: {
                WITHDRAW: '',
                TERMINATE: '',
                TRANSITION: '',
                // SUSPEND_OPERATE: '',
                SUSPEND: '',
                // UNSUSPEND_OPERATE: '',
                UNSUSPEND: '',
                DELIVER: '',
                CLAIM: '',
                DISTRIBUTE: '',
                FOLLOW: '',
                FINISHED: ''
            }
        }
    },
    props: {
        isshow_: {
            type: Boolean,
            default () {
                return true
            }
        },
        stateId: {
            type: Number,
            default () {
                return null
            }
        },
        nodeinfo: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    async created () {
        this.service = this.$route.query.service
    },
    computed: {},
    methods: {
        getValue (item) {
            if (item.type === 'CASCADE') {
                if (item.value === '') {
                    return ''
                } else {
                    let valCas = ''
                    let valStatus = true
                    for (let s = 0; s < item.choice.length; s++) {
                        if (String(item.choice[s].key) === String(item.value)) {
                            valStatus = false
                            valCas = item.choice[s].name
                            return valCas
                        }
                    }
                    if (valStatus) {
                        for (let j = 0; j < item.choice.length; j++) {
                            if (item.choice[j].items) {
                                let valInfo = item.choice[j].items
                                for (let t = 0; t < valInfo.length; t++) {
                                    if (String(valInfo[t].key) === String(item.value)) {
                                        valCas = item.choice[j].name + '->' + valInfo[t].name
                                        return valCas
                                    }
                                }
                            }
                        }
                    }
                }
            } else {
                return item.display_value
            }
        },
        getValues (item) {
            var values = []
            var array = []
            if (item.value) {
                array = item.value.split(',')
            }
            item.choice.forEach(obj => {
                array.forEach(it => {
                    if (obj.key === it) {
                        values.push(obj.name)
                    }
                })
            })
            return values.toString()
        },
        cutmessage (message) {
            // console.log(message.indexOf(''))
            // message.split('')[1].split('')[0]
            if (message.indexOf('') !== -1) {
                let ems = message.split('')[1]
                ems = ems.split('')[0]
                return ems
            } else {
                return ''
            }
        }
    }
}



