export default {
    name: 'billCom',
    data () {
        return {
            // ,
            OPERATE_TYPE: {
                WITHDRAW: '',
                TERMINATE: '',
                TRANSITION: '',
                // SUSPEND_OPERATE: '',
                SUSPEND: '',
                // UNSUSPEND_OPERATE: '',
                UNSUSPEND: '',
                DELIVER: '',
                // CLAIM: '',
                CLAIM: '',
                // DISTRIBUTE: '',
                DISTRIBUTE: '',
                FOLLOW: '',
                FINISHED: '',
                REPULSE: ''
            },
            greenList: ['TRANSITION'],
            redList: ['TERMINATE', 'SUSPEND', 'REPULSE'],
            blueList: ['UNSUSPEND', 'DELIVER', 'CLAIM', 'DISTRIBUTE', 'FOLLOW'],
            grayList: ['WITHDRAW', 'FINISHED']
        }
    },
    props: {
        billinfoOri: {
            type: Object,
            default () {
                return {}
            }
        },
        buttoninfoOri: {
            type: Object,
            default () {
                return {
                    content: '',
                    status: ''
                }
            }
        },
        // routeName: String,
        submit: Function
    },
    computed: {
        // billinfo () {
        //     let billinfo
        //     return billinfo
        // }
    },
    async mounted () {
        this.changedate()
    },
    methods: {
        async changedate () {
            let value = this.$route.name === 'processingRecords' ? this.billinfoOri.operate_at : this.billinfoOri.create_at
            this.timet = value
            // await this.$store.dispatch('changdate', value).then((res) => {
            //     // console.log(res)
            //     this.timet = res
            // })
        },
        isColor (type) {
            if (this.greenList.indexOf(type) !== -1) {
                return 'green'
            } else if (this.redList.indexOf(type) !== -1) {
                return 'red'
            } else if (this.grayList.indexOf(type) !== -1) {
                return 'gray'
            } else {
                return 'blue'
            }
        },
        cutmessage (message) {
            if (!message) {
                return ''
            }
            // console.log(message.indexOf(''))
            // message.split('')[1].split('')[0]
            if (message.indexOf('') !== -1) {
                let ems = message.split('')[1]
                ems = ems.split('')[0]
                return ems
            } else {
                return ''
            }
        }
        // submit(){
        //     console.log('')
        //     // window.confirm('111')
        //     // console.log(this.$parent.$parent)
        //     this.$parent.$parent.ishandle = true
        //     // console.log(this.$parent.$parent.ishandle)
        // }
    }
}



