/**
*  app-exception
*  @desc 
*  @param type {String} - 404403500building
*  @param delay {Number} - 
*  @param text {String} - 404403Sorry500building()
*  @example1 <app-exception type="404"></app-exception>
*/
import img403 from '../../../images/403.png'
import img404 from '../../../images/404.png'
import img500 from '../../../images/500.png'
import imgBuilding from '../../../images/building.png'

export default {
    name: 'app-exception',
    props: {
        type: {
            type: String,
            default: '404'
        },
        delay: {
            type: Number,
            default: 0
        },
        text: {
            type: String,
            default: ''
        }
    },
    data () {
        let message = ''
        let image = ''

        switch (this.type) {
            case '403':
                image = img403
                message = 'Sorry'
                break

            case '404':
                image = img404
                message = ''
                break

            case '500':
                image = img500
                message = '!'
                break

            case 'building':
                image = imgBuilding
                message = ''
                break
        }

        if (this.text) {
            message = this.text
        }
        
        return {
            show: false,
            message: message,
            image: image
        }
    },
    created () {
        setTimeout(() => {
            this.show = true
        }, this.delay)
    }
}



